import Vue from 'vue'
import Router from 'vue-router'
import pos from '@/components/pos.vue'
import leftNav from '@/components/common/leftNav.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'pos',
      component: pos
    }
  ]
})



