<template>
  <div class="pos"> 
    <el-row>
      <el-col :span='7' class="pos-order" id='order-list'>
        <el-tabs>
          <el-tab-pane label='点餐'>
            <el-table  :data="tableData" border style="width: 100%">
              <el-table-column label='商品名称' prop='goodsName'>商品名称</el-table-column>
              <el-table-column label='数量' prop='count' width='50'>数量</el-table-column>
              <el-table-column label='金额' prop='price'  width='70'>金额</el-table-column>
              <el-table-column label='操作'  width='100' fixed='right'>
                <template slot-scope="scope">
                  <el-button type='text' size='small' @click=" delSingleGoods(scope.row)">删除</el-button>
                  <el-button type='text' size='small' @click="addOrderList(scope.row)">增加</el-button>
                </template>
              </el-table-column>
            </el-table>
            <div>
              数量：{{totalCount}} &nbsp  总计金额：{{totalMoney}}
            </div>
            <div class="div-btn">
              <el-button type='warning'>挂单</el-button>
              <el-button type='danger'>删除</el-button>
              <el-button type='success'>结账</el-button>
            </div>
          </el-tab-pane>
          <el-tab-pane label='挂单'>挂单</el-tab-pane>
          <el-tab-pane label='外卖'>外卖</el-tab-pane>
        </el-tabs>
      </el-col>
      <el-col :span='17'>
        <div class='often-goods'>
          <div class="title">常用商品</div>
          <div class="often-goods-list">
            <ul >
              <li v-for="val in oftenGoods" @click="addOrderList(val)">
                <span>{{val.goodsName}}</span>
                <span class='o-price'>{{ val.price}}</span>
              </li>
            </ul>
          </div>
        </div>
        <div class='goods-type'>
          <el-tabs>
            <el-tab-pane label='汉堡'>
              <ul class="cookList">
                <li v-for="val in type0Goods" @click="addOrderList(val)">
                  <span class='foodImg'><img :src="val.goodsImg" width="100%"></span>
                  <span class="foodName">{{val.goodsName}}</span>
                  <span class="foodPrice">￥{{val.price}}元</span>
                </li>
              </ul>
            </el-tab-pane>
            <el-tab-pane label='小食'>
              <ul class="cookList">
                <li v-for="val in type1Goods" @click="addOrderList(val)">
                  <span class='foodImg'><img :src="val.goodsImg" width="100%"></span>
                  <span class="foodName">{{val.goodsName}}</span>
                  <span class="foodPrice">￥{{val.price}}元</span>
                </li>
              </ul>
            </el-tab-pane>
            <el-tab-pane label='饮料'>
              <ul class="cookList">
                <li v-for="val in type2Goods" @click="addOrderList(val)">
                  <span class='foodImg'><img :src="val.goodsImg" width="100%"></span>
                  <span class="foodName">{{val.goodsName}}</span>
                  <span class="foodPrice">￥{{val.price}}元</span>
                </li>
              </ul>
            </el-tab-pane>
            <el-tab-pane label='套餐'>
              <ul class="cookList">
                <li v-for="val in type3Goods"  @click="addOrderList(val)">
                  <span class='foodImg'><img :src="val.goodsImg" width="100%"></span>
                  <span class="foodName">{{val.goodsName}}</span>
                  <span class="foodPrice">￥{{val.price}}元</span>
                </li>
              </ul>
            </el-tab-pane>
          </el-tabs>
        </div>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import axios from "axios";
export default {
  name: "pos",
  created: function() {
    axios
      .get("http://jspang.com/DemoApi/oftenGoods.php")
      .then(response => {
        console.log(response);
        this.oftenGoods = response.data;
      })
      .catch(function(error) {
        console.log(error);
        alert("网络错误，无法访问");
      });
    axios
      .get("http://jspang.com/DemoApi/typeGoods.php")
      .then(response => {
        console.log(response);
        this.type0Goods = response.data[0];
        this.type1Goods = response.data[1];
        this.type2Goods = response.data[2];
        this.type3Goods = response.data[3];
      })
      .catch(function(error) {
        console.log(error);
        alert("网络错误，无法访问");
      });
  },
  mounted: function() {
    var orderHeight = document.body.clientWidth;
    document.getElementById("order-list").style.height = orderHeight + "px";
  },
  data() {
    return {
      tableData: [],
      oftenGoods: [],
      type0Goods: [],
      type1Goods: [],
      type2Goods: [],
      type3Goods: [],
      totalMoney: 0,
      totalCount: 0
    };
  },
  methods: {
    // 删除单个商品
    delSingleGoods(goods) {
      this.tableData = this.tableData.filter(o => o.goodsId != goods.goodsId);
       this.getAllMoney()
    },
    // 汇总金额和数量
    getAllMoney() {
      this.totalMoney = 0;
      this.totalCount = 0;
      if (this.tableData) {
        for (let i = 0; i < this.tableData.length; i++) {
          this.totalCount += this.tableData[i].count;
          this.totalMoney =
            this.totalMoney + this.tableData[i].count * this.tableData[i].price;
        }
      }
    },
    addOrderList(goods) {
      // 汇总清零
      this.totalMoney = 0;
      this.totalCount = 0;
      // 先判断列表是否有商品
      let isHave = false;
      for (let i = 0; i < this.tableData.length; i++) {
        if (this.tableData[i].goodsId == goods.goodsId) {
          isHave = true;
        }
      }
      // 根据判断来写业务逻辑
      if (isHave) {
        //列表中存在的业务处理
        //改变列表中商品数量
        let arr = this.tableData.filter(o => o.goodsId == goods.goodsId);
        console.log(arr);
        arr[0].count++;
      } else {
        let newGoods = {
          goodsId: goods.goodsId,
          goodsName: goods.goodsName,
          price: goods.price,
          count: 1
        };
        this.tableData.push(newGoods);
      }
      // 列表的总数量和总金额
      //  for(let i=0;i<this.tableData.length;i++){
      //     this.totalCount+=this.tableData[i].count;
      //     this.totalMoney=this.totalMoney+(this.tableData[i].count*this.tableData[i].price)
      //  }
       this.getAllMoney()
    }
  }
};
</script>
<style scoped>
.pos-order {
  background-color: #f9fafc;
  border-right: 1px solid #c0ccda;
}
.div-btn {
  margin-left: 30px;
}
.title {
  height: 20px;
  border-bottom: 1px solid #d3dce6;
  background-color: #f9fafc;
  padding: 10px;
}
.often-goods-list ul li {
  list-style: none;
  float: left;
  border: 1px solid #e5e9f2;
  padding: 10px;
  margin: 5px;
  background-color: #fff;
  cursor: pointer;
}
.o-price {
  color: #58b7ff;
}
.goods-type {
  clear: both;
}
.cookList li {
  list-style: none;
  width: 23%;
  border: 1px solid #e5e9f2;
  height: auot;
  overflow: hidden;
  background-color: #fff;
  padding: 2px;
  float: left;
  margin: 2px;
  cursor: pointer;
}
.cookList li span {
  display: block;
  float: left;
}
.foodImg {
  width: 40%;
}
.foodName {
  font-size: 18px;
  padding-left: 10px;
  color: brown;
}
.foodPrice {
  font-size: 16px;
  padding-left: 10px;
  padding-top: 10px;
}
</style>




